@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  min-height: 100%;
  position: relative;
  scroll-behavior: smooth;
}

:root {
  --font-family: "Roboto", sans-serif;
}

.sidebar {
  /* border: 2px solid yellow; */
  flex: 3 1;
  position: absolute;
  z-index: 20;
  width: 26%;
  /* height: 90%; */
  background-color: rgb(255, 255, 255);
  /* overflow-y: scroll; */
  top: 50px;
  transition: all 0.8s ease-in-out;
}

/* to hide scorollbar */
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  background-color: rgb(230, 230, 230);
}
.expand_collapse_container {
  /* border: 2px solid red; */
  position: absolute;
  z-index: 70;
  left: 10px;
  width: 26%;
  height: 1%;
  transition: all 0.8s ease-in-out;
}
.collapseContainer {
  /* border: 2px solid blue; */
  width: 4%;
  transition: all 0.8s ease-in-out;
}
.expand_coallapseImg {
  cursor: pointer;
  width: 40px;
  height: 40px;
  transition: all 0.8s ease-in-out;
}
.sidebarWrapper {
  background-color: rgb(255, 249, 249);
  position: fixed;
  padding: 20px;
  width: 25%;
  height: 100vh;
  z-index: 40;
  overflow-y: scroll;
  transition: all 0.8s ease-in-out;
}
.sidebarWrapperHide {
  display: none !important;
  top: 50px;
  transition: all 0.8s ease-in-out;
}
.sidebarCollapse {
  /* border: 2px solid red; */
  display: none;
  position: relative;
  width: 3%;
  height: 100%;
  transition: all 0.8s ease-in-out;
}

.sidebarList {
  padding: 0;
  margin: 0;
  list-style: none;
}
.sidebarListItem {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.sidbebarIcons {
  margin-right: 15px;
}
.sidebarBtn {
  width: 150px;
  border: none;
  padding: 10px;
  border-radius: 5px;
  font-weight: 500;
}
.sidebarHr {
  margin: 20px 0;
}
.sidebarTitlecontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.sidebarTitle {
  font-size: 1.5rem;
  font-weight: 500;
  font-family: var(--font-family);
}

.sidebarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}

.sidebarFriend {
  /* border: 2px solid red; */
  display: flex;
  width: 70%;
  justify-content: flex-start;
  align-items: center;
}
.sidebarFriendName {
  font-size: 1.2rem;
  font-weight: bold;
  margin: auto 0;
}
.sidebarFriendImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.rightBarFollowBtn {
  height: 30px;
  width: 100px;
  border-radius: 10px;
  border: none;
  background-color: #1775ee;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 500;
  cursor: pointer;
  margin: 0.5rem;
}
.rightbarFollowersection {
  /* border: 2px solid yellow; */
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.rightBarbtntxt {
  font-size: 1rem;
}
.rightBarbtntxt {
  margin: 0.2rem 1rem;
  font-size: 1.1rem;
  font-weight: 500;
}

.slide-left {
  -webkit-animation: slide-left 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955)
    both;
  animation: slide-left 0.8s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
}
@-webkit-keyframes slide-left {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px);
  }
}
@keyframes slide-left {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100px);
    transform: translateX(-100px);
  }
}

